.wrapper {
  padding: 20px;
}

.progress-wrap {
  width: 100%;
  height: 30px;
  background-color: #cccccc;
  position: relative;
}

.progress-bar {
  width: 0%;
  height: 100%;
  background-color: red;
}

.transition {
  -webkit-transition: width 1684ms linear 0s;
  -o-transition: width 1684ms linear 0s;
  transition: width 1684ms linear 0s
}

.animation {
  -webkit-animation: progress 1684ms linear 0s infinite;
          animation: progress 1684ms linear 0s infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

@-webkit-keyframes progress {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes progress {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

.progress-canvas, .progress-svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}


